<template>
  <!--运动库-->
  <div>
    <div class="demo-form-inline">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="关键字">
          <el-input v-model="searchForm.key" placeholder="编码/项目名称"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getSearchBtn()">查 询</el-button>
          <el-button type="primary" icon="el-icon-plus" @click="onSubmit">新 增</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--列表主题-->
    <el-table :data="tableData" style="width: 100%" >
        <el-table-column
          type="selection"
          width="55">
      </el-table-column>
          <el-table-column prop="mno" label="编码"></el-table-column>
          <el-table-column prop="mname" label="项目名称"></el-table-column>
          <el-table-column prop="mstrength" label="运动强度"></el-table-column>
          <el-table-column prop="mweight" label="千步当量"></el-table-column>
          <el-table-column prop="magelower" label="年龄下限"></el-table-column>
          <el-table-column prop="mageupper" label="年龄上线"></el-table-column>
          <el-table-column prop="mtime" label="运动时间"></el-table-column>

          <el-table-column
            fixed="right"
            label="操作"
            width="100">
            <template slot-scope="scope">
              <el-button @click="editRow(scope.row)" type="text" size="small">编辑</el-button>
              <el-button @click="deleteRow(scope.row)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
    </el-table>

    <!--分页-->
    <el-pagination class="mt-20p"
                   background
                   :current-page="searchForm.page"
                   :page-size="searchForm.size"
                   layout="->, total, prev, pager, next"
                   :total="total"
      @current-change="handleCurrentChange"/>
    <!-- <MovementBaseDialog ref="MovementBaseDialog"></MovementBaseDialog> -->
  
  </div>
</template>

<script>
  // import servicesApi from "../../services/servicesApi";
  // import MovementBaseDialog from '../../components/KnowledgeBaseDialog/MovementBaseDialog'
  export default {
    data() {    
      return {
        // 查询条件
        searchForm:{
        key:'',
        page:1,
        size:10
        },
        //表中展示的数据 
        tableData:[],
        //分页中的总数 
        total:13
      }
    },
    mounted() {
      this.getMovementListApi()
    },
    //页面所属组件 
    // components:{
    //   MovementBaseDialog,
    // },
    methods: {
      //获取所有数据
      getMovementListApi(){
        servicesApi.getMovementListApi().then((res)=>{
          this.tableData = res.data.data;
        })
      }, 
      //删除所选择的数据[后期根据返回值来判断是否删除成功]
      deleteRow(row){
        console.log(row);
        servicesApi.deleteMovementListApi(row.mno).then((res)=>{
          this.getMovementListApi();
          console.log(res);
        })
      },
      //搜索 
      getSearchBtn(){

      },
      //新增
      onSubmit(){
          
          this.$refs['MovementBaseDialog'].show();
          
      },
      //编辑 
      editRow(row){

      this.$refs.MovementBaseDialog.show(row);
      },



      handleClick(row) {
        console.log(row);
      },
      handleCurrentChange(){
        
      },


    },
  }
</script>